/*==========  MEDIA QUERIES  ==========*/
// @media (max-width: 1368px)

@media (max-width: 1199px) {
    .healthy-left {
        width: 550px;
        height: 550px;
        padding: 35px;
        margin: 0 auto 30px auto;
        .image {
            transform: translate(-50%, -50%); }
        .gradient-item {
            display: none; } }

    .healthy-info-wrap {
        padding: 0; }
    .healthy-info {
        padding-top: 0;
        min-height: 0;
        margin-bottom: 20px;
        &:last-of-type {
            margin-bottom: 0; }
        .title {
            -webkit-line-clamp: unset; }
        .text {
            -webkit-line-clamp: unset; } } }

