/*==========  MEDIA QUERIES  ==========*/

@media (max-width: 1368px) {
    .desert-btn-prev {
        height: 260px;
        width: 130px;
        .icon-pr {
            width: 70px; }
        .swiper-button-prev {
            left: 15px; } }
    .desert-btn-next {
        height: 260px;
        width: 130px;
        .icon-pr {
            width: 70px; }
        .swiper-button-next {
            right: 15px; } } }

@media (max-width: 1199px) {
    .desert-btn-next,
    .desert-btn-prev {
        display: none; } }

@media (max-width: 991px) {
    .real-desert-item {
        .image {
            text-align: center;
            line-height: 0;
            margin-bottom: 30px; } } }

