/*==========  Hover  ==========*/

@media (min-width: 1200px) {
    .real-fruits-list li:hover {
        color: $mainColor;
        .icon {
            fill: $mainColor; } } }


/*==========  MEDIA QUERIES  ==========*/
@media (max-width: 1368px) {
    .real-fruits-content .text-wrap {
        padding-right: 20px; } }

@media (max-width: 1199px) {
    .real-fruits-sc {
        .br-left {
            border-left: 0; } }
    .real-fruits-content {
        padding: 0;
        flex-wrap: wrap;
        .content {
            padding: 0;
            flex: 0 0 100%;
            order: 1; }
        .image {
            flex: 0 0 100%;
            text-align: center;
            line-height: 0;
            margin-bottom: 30px; }
        .text-wrap {
            padding-left: 40px;
            padding-right: 0; }
        .number {
            font-size: 18px;
            width: 30px;
            height: 30px;
            line-height: 30px;
            top: -1px; } }
    .real-fruits-list {
        display: none; }
    .real-fruits-sc {
        .swiper-pagination {
            display: block; }
        .real-fruits-button-wr {
            display: none; } } }

