/* GLOBAL SETTINGS */
/*-------------------------------------------------------------------------------------------------------------------------------*/

html {
	height: auto; }
body {
	height: auto;
	overflow: visible; }
body ::selection {
	color: #fff;
	background: #000; }
body ::-moz-selection {
	color: #fff;
	background: #000; }
.swiper-slide:not(:first-child) {
	display: block; }

