/*==========  MEDIA QUERIES  ==========*/

// @media (max-width: 1368px)

@media (max-width: 1199px) {
    .our-benefits-image {
        margin-bottom: 30px;
        .point-wrap {
            display: none; } }
    .our-benefits-text {
        position: relative;
        overflow: visible;
        padding-left: 20px;
        min-height: 0;
        -webkit-line-clamp: unset;
        &:last-of-type {
            margin-bottom: 15px; }
        .point {
            display: inline-block;
            width: 8px;
            height: 8px;
            background: linear-gradient(52deg, #91b428 0%, #4f981e 100%);
            border-radius: 50%;
            position: absolute;
            left: 0;
            top: 0.5em;
            &::before {
                content: '';
                display: block;
                position: absolute;
                width: calc(100% + 6px);
                height: calc(100% + 6px);
                border: 1px solid #4f981e;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                border-radius: 50%; } } } }


// @media (max-width: 991px)

// @media (max-width: 768px)

@media (max-width: 575px) {
    .our-benefits-image {
        display: none; }
